import IconButton from "@/components/icon-button.vue"
import Popup from "@/components/popup.vue"

export default {
	props: {
		title: {
			type: String,
			default: ''
		},
		fixed: {
			type: Boolean,
			default: true
		},
		noreadnum: {
			type: Number,
			default: 0
		}
	},
	components: {
		IconButton,
		Popup
	},
	computed: {
		barHeightStyle() {
			return `height: ${this.navBarHeight}px`
		},
		getTitle() {
			return `${this.title}${this.noreadnum > 0 ? `(${this.noreadnum})` : ''}`
		}
	},
	data() {
		return {
			appStatusBarHeight: 0,
			navBarHeight: 0,
			menus: [
				{
					name: '发起群聊',
					event: 'setTop',
					icon: '\ue633'
				},
				{
					name: '添加朋友',
					event: 'delChat',
					icon: '\ue65d'
				},
				{
					name: '扫一扫',
					event: 'delChat',
					icon: '\ue614'
				},
				{
					name: '收付款',
					event: 'delChat',
					icon: '\ue66c'
				},
				{
					name: '帮助与反馈',
					event: 'delChat',
					icon: '\ue62a'
				}
			]
		}
	},
	mounted() {



		// 由于动态获取的值是 px，而我们自己设置的头是 rpx，因此需要做转换，统一转成 px
		// PS: upx 就是 rpx
		this.navBarHeight = this.appStatusBarHeight + uni.upx2px(90)
	},
	methods: {
		open() {
			this.$emit('open')
		},
		openExtend() {
			this.$refs.popup.show(uni.upx2px(415), uni.upx2px(50))
		}
	}
}
